
#app {
  background-color: #111;
  width: 460px;
  height: 780px;
  padding: 10px;
  font-size: 20px;
}
.rows {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.rows .row {
    margin-bottom: 10px;
}
.rows .row:last-child {
      margin-bottom: 0;
}
.rows .header {
    font-size: 24px;
    font-weight: bold;
    line-height: 40px;
    background-color: #333;
    border: 1px solid white;
    color: white;
    text-align: center;
}
.rows .button-indicators {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.rows .button-indicators div {
      border-bottom-right-radius: 10px;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      border: 1px solid #666;
      background-color: #000;
      color: #999;
      line-height: 60px;
      text-align: center;
      margin-right: 10px;
}
.rows .button-indicators div.is-active {
        border-color: #ffffff;
        background-color: #666666;
        color: white;
}
.rows .button-indicators div.is-active.is-red {
          border-color: #ff0000;
          background-color: #660000;
}
.rows .button-indicators div.is-active.is-green {
          border-color: #00ff00;
          background-color: #006600;
}
.rows .button-indicators div.is-active.is-yellow {
          border-color: #ffff00;
          background-color: #666600;
}
.rows .button-indicators div.is-active.is-purple {
          border-color: #ff00ff;
          background-color: #660066;
}
.rows .button-indicators div:last-child {
        margin-right: 0;
}
.rows .button-indicators:last-child {
      margin-bottom: 0;
}
.rows .slider {
    height: 30px;
    border: 1px solid #00ffff;
    background-color: #003333;
    padding-right: 30px;
}
.rows .slider .indicator-wrapper {
      position: relative;
}
.rows .slider .indicator-wrapper .indicator {
        -webkit-transition: left 0.25s;
        transition: left 0.25s;
        position: absolute;
        width: 30px;
        height: 30px;
        background-color: #00ffff;
}
.rows .console {
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    font-size: 13px;
    line-height: 13px;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border: 1px solid #666;
    background-color: black;
    overflow: hidden;
    position: relative;
}
.rows .console .content {
      position: absolute;
      padding: 10px;
      width: 100%;
      bottom: 0;
}
.rows .console .content .timestamp {
        color: #666;
}
.rows .console .content .message {
        color: #999;
}
.rows .console .content .payload {
        color: #333;
        font-size: 10px;
}
.rows .footer {
    font-size: 12px;
    line-height: 30px;
    background-color: black;
    border: 1px solid #333;
    text-align: center;
}
